nav {
  font-family: 'Urbanist', sans-serif;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: #121824cc;
  color: white;
  padding: 1rem 1rem; 
  z-index: 1000;
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.3);
  border-radius: 0 0 10px 10px;
  backdrop-filter: blur(10px);
}

ul {
  list-style: none;
  display: flex;
  justify-content: center; 
  align-items: center; 
  margin: 0;
  padding: 0;
}

li {
  cursor: pointer;
  margin-left: 1rem;
}

li:first-child {
  margin-left: 0; 
}

li a {
  color: white;
  text-decoration: none;
  transition: color 0.3s; 
  padding: 0.5rem 1rem;
  font-size: 16px;
  line-height: 1;
  letter-spacing: 1px;
}

li a:hover {
  color: #81A5EB; 
}

@media (min-width: 768px) {
  ul {
      margin: 0 5%;
  }
}

@media (max-width: 768px) {
  nav {
      padding: 0.5rem;
  }
  
  ul {
      flex-wrap: wrap; 
      justify-content: center; 
      padding: 0;
  }
  
  li {
      margin: 0.5rem;
      font-size: 18px;
  }

  li a {
      font-size: 18px; 
      padding: 0.4rem 0.8rem; 
  }
}

@media (max-width: 480px) {
  nav {
      padding: 0.5rem;
  }
  
  ul {
      flex-direction: row; 
      justify-content: center; 
      align-items: center; 
  }
  
  li {
      margin: 0 0.5rem; 
  }

  li a {
      font-size: 12px; 
      padding: 0.3rem 0.6rem; 
  }
}
